.containerHeader {
  justify-content: space-between;
  align-items: center;
  width: auto;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  display: flex;
}

.containerHeader__logo {
  height: 40px;
}

.navigation {
  height: 60px;
}

.navigation__list {
  color: #060b35;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation__item:not(:last-child) {
  margin-right: 20px;
}

.navigation__item:first-child {
  color: #00acdc;
}

.navigation__item .navigation__link {
  color: inherit;
  text-transform: uppercase;
  height: 100%;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.navigation__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  top: 56px;
}

.container {
  box-sizing: border-box;
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 1023px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width <= 768px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width <= 488px) {
  .container {
    grid-template-columns: repeat(1, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  background-repeat: no-repeat;
  background-size: contain;
  width: 160px;
  height: 134px;
  margin: 32px 19px 36px;
}

.card__title {
  color: #060b35;
  width: 166px;
  height: 36px;
  margin: 0 16px 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
}

.card__subtitle {
  color: #616070;
  max-width: 98px;
  min-height: 14px;
  margin: 0 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  box-sizing: border-box;
  text-align: center;
  justify-content: space-between;
  width: 166px;
  height: 16px;
  margin: 0 auto 24px;
  padding: 2px 0 0 2px;
  display: flex;
}

.card__reviews-text {
  text-align: right;
  color: #060b35;
  width: 53px;
  height: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 500;
  line-height: 14px;
}

.card__reviews-stars {
  box-sizing: border-box;
  border-radius: .5px;
  width: 96px;
  height: 16px;
  display: flex;
}

.card__reviews-star {
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.card__reviews-star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  margin-right: 4px;
}

.card__prices {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 18px;
  margin: 0 auto 16px;
  display: flex;
}

.card__prices-text {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__prices-value {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  min-height: 40px;
  margin: 0 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

body {
  margin: 0;
}
